<template>
    <div class="c_procedure_set_up">
        <div class="c_set_up_header">
            <CContentHeader :isShowIcon="true" content="流程控制" />
            <el-button class="c_set_up_header_btn" @click="nextStep" type="primary">下一步</el-button>
        </div>
        <div class="c_set_up_mian flex">
            <!-- 左边图标模块 -->
            <div class="c_set_up_left">
                <h4>控件库</h4>
                <div class="c_set_up_left_main">
                    <div :class="['c_set_up_left_iocn',activeClass == index ? 'active':'']" v-for="(item,index) in controlList" :key="index" @click="getItem(index,item)">
                        <img :src="require(`../../assets/img/procedure/${item.type}.png`)" alt="" srcset="">
                    </div>
                    <!-- <div class="c_set_up_left_iocn">
                        <img src="../../assets/img/procedure/组32.png" alt="" srcset="">
                    </div>
                    <div class="c_set_up_left_iocn">
                        <img src="../../assets/img/procedure/组33.png" alt="" srcset="">
                    </div>
                    <div class="c_set_up_left_iocn">
                        <img src="../../assets/img/procedure/组34.png" alt="" srcset="">
                    </div>
                    <div class="c_set_up_left_iocn">
                        <img src="../../assets/img/procedure/组35.png" alt="" srcset="">
                    </div>
                    <div class="c_set_up_left_iocn">
                        <img src="../../assets/img/procedure/组36.png" alt="" srcset="">
                    </div>
                    <div class="c_set_up_left_iocn">
                        <img src="../../assets/img/procedure/组37.png" alt="" srcset="">
                    </div>
                    <div class="c_set_up_left_iocn">
                        <img src="../../assets/img/procedure/组38.png" alt="" srcset="">
                    </div>
                    <div class="c_set_up_left_iocn">
                        <img src="../../assets/img/procedure/组39.png" alt="" srcset="">
                    </div>
                    <div class="c_set_up_left_iocn">
                        <img src="../../assets/img/procedure/组40.png" alt="" srcset="">
                    </div>
                    <div class="c_set_up_left_iocn">
                        <img src="../../assets/img/procedure/组41.png" alt="" srcset="">
                    </div>
                    <div class="c_set_up_left_iocn">
                        <img src="../../assets/img/procedure/组42.png" alt="" srcset="">
                    </div>
                    <div class="c_set_up_left_iocn">
                        <img src="../../assets/img/procedure/组43.png" alt="" srcset="">
                    </div> -->
                </div>
            </div>
            <!-- 中间内容模块 -->
            <div class="c_set_up_center">
                <div class="c_model">
                    <div class="c_model_title">
                        <span v-show="templateName != ''">{{templateName}}</span>
                        <span v-show="templateName == ''">未命名模板</span>
                    </div>
                    <div class="c_model_content">
                        <div v-if="!list.length" class="c_model_tips">
                            请点击左侧控件来创建您需要的模板
                        </div>
                        <div v-for="(item,index) in list" :key="index" class="c_model_item" :class="[{active : item.isShow},{toast_warning:item.isRepeat}]" @click="setModel(item,index)">
                            <!-- 说明文字 -->

                            <div v-if="item.type === 'tips'" class="c_tips">
                                {{item.explain}}
                            </div>

                            <!-- 部门  成员  多选框  单选框 -->

                            <div :class="'c_'+item.type+' c_model_item_box'" v-else-if="item.type === 'department' || item.type === 'user' || item.type === 'checkbox' || item.type === 'select' ">
                                <div class="c_model_module_title">
                                    {{item.name}}
                                </div>
                                <div class="c_model_item_right">
                                    <div v-if="!item.explain" class="c_textarea_describe">
                                        请输入
                                    </div>
                                    <div v-else class="c_textarea_describe">
                                        {{item.explain}}
                                    </div>
                                    <div class="c_model_item_icon">
                                        <i class="el-icon-arrow-right"></i>
                                    </div>
                                </div>
                            </div>

                            <!-- 附件 -->
                            <div v-else-if="item.type === 'file' " class="c_file">
                                <div class="c_textarea_title">
                                    {{item.name}}
                                </div>
                                <div class="c_file_icon">
                                    <i class="el-icon-paperclip"></i>
                                </div>
                            </div>

                            <!-- 多行文本 -->

                            <div v-else-if="item.type === 'textarea' " class="c_textarea">
                                <div class="c_textarea_title">
                                    {{item.name}}
                                </div>
                                <div v-if="!item.explain" class="c_textarea_describe">
                                    请输入
                                </div>
                                <div v-else class="c_textarea_describe">
                                    {{item.explain}}
                                </div>
                            </div>



                            <!-- 明细 -->
                            <div v-else-if="item.type === 'detailed' " class="c_detailed" :class="{select_parent_control: isAddDetailed && listIndex ===index}">
                                <div class="c_detailed_title">
                                    {{item.name}}
                                </div>
                                <div class="c_detailed_content">
                                    <div v-if="item.detailed.length === 0" class="c_detailed_explain">
                                        可添加多个控件(不包含明细控件)
                                    </div>
                                    <div class="c_detailed_module" :class="[{active : detailedItem.isShow},{toast_warning:detailedItem.isRepeat}]" v-for="(detailedItem,detailedIndex) in item.detailed" :key="detailedIndex" @click.stop="setModel(detailedItem,index,detailedIndex)">
                                        <!-- 说明文字 :class="{active : detailedItem.isShow}" -->
                                        <div class="bdb">
                                            <div v-if="detailedItem.type === 'tips'" class="c_tips no-pl">
                                                {{detailedItem.explain}}
                                            </div>

                                            <!-- 部门  成员  多选框  单选框 -->

                                            <div :class="'c_'+detailedItem.type+' c_model_item_box no-pl'" v-else-if="detailedItem.type === 'department' || detailedItem.type === 'user' || detailedItem.type === 'checkbox' || detailedItem.type === 'select' ">
                                                <div class="c_model_module_title">
                                                    {{detailedItem.name}}
                                                </div>
                                                <div class="c_model_item_right">
                                                    <div v-if="!detailedItem.explain" class="c_textarea_describe">
                                                        请输入
                                                    </div>
                                                    <div v-else class="c_textarea_describe">
                                                        {{detailedItem.explain}}
                                                    </div>
                                                    <div class="c_model_item_icon">
                                                        <i class="el-icon-arrow-right"></i>
                                                    </div>
                                                </div>
                                            </div>

                                            <!-- 附件 -->
                                            <div v-else-if="detailedItem.type === 'file' " class="c_file no-pl">
                                                <div class="c_textarea_title">
                                                    {{detailedItem.name}}
                                                </div>
                                                <div class="c_file_icon">
                                                    <i class="el-icon-paperclip"></i>
                                                </div>
                                            </div>

                                            <!-- 多行文本 -->

                                            <div v-else-if="item.type === 'textarea' " class="c_textarea no-pl">
                                                <div class="c_textarea_title">
                                                    {{detailedItem.name}}
                                                </div>
                                                <div v-if="!detailedItem.explain" class="c_textarea_describe">
                                                    请输入
                                                </div>
                                                <div v-else class="c_textarea_describe">
                                                    {{detailedItem.explain}}
                                                </div>
                                            </div>

                                            <!-- 文本  数字  日期  日期+时间  金额 -->
                                            <div v-else :class="'c_'+detailedItem.type+' c_model_item_box no-pl'">
                                                <div class="c_model_module_title">
                                                    {{detailedItem.name}}
                                                </div>
                                                <div v-if="!detailedItem.explain" class="c_textarea_describe">
                                                    请输入
                                                </div>
                                                <div v-else class="c_textarea_describe">
                                                    {{detailedItem.explain}}
                                                </div>
                                            </div>
                                            <div class="c_detailed_icon" @click.stop="delItem(detailedItem,index,detailedIndex)">
                                                <img src="../../assets/img/procedure/del2.png" alt="">
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="c_detailes_btn" @click.stop="addDetailed(index)">
                                    添加子控件
                                </div>

                            </div>
                            <!-- 文本  数字  日期  日期+时间  金额 -->
                            <div v-else :class="'c_'+item.type+' c_model_item_box'">
                                <div class="c_model_module_title">
                                    {{item.name}}
                                </div>
                                <div v-if="!item.explain" class="c_textarea_describe">
                                    请输入
                                </div>
                                <div v-else class="c_textarea_describe">
                                    {{item.explain}}
                                </div>
                            </div>

                            <div class="c_model_module_icon" @click.stop="delItem(item,index)">
                                <img src="../../assets/img/procedure/del2.png" alt="">
                            </div>
                        </div>

                    </div>
                </div>
            </div>
            <!-- 右边表单模块 -->
            <div class="c_set_up_right">
                <h4>模板设置</h4>
                <div class="c_set_up_right_main">
                    <div class="c_template">
                        <div class="c_template_name">
                            <div class="c_title">
                                模板名称：
                            </div>
                            <div class="c_input c_input_color">
                                <el-input v-model="templateName" placeholder="请输入内容"></el-input>
                            </div>
                        </div>
                        <div class="c_template_icon">
                            <div class="c_title">
                                模板图标：
                            </div>
                            <div class="c_icon">
                                ICON
                            </div>
                        </div>
                    </div>

                    <div v-if="controlItem.isShow" class="c_control_content">
                        <div class="c_control">
                            <div class="c_control_style">
                                <div class="c_title">
                                    控件类型：
                                </div>
                                <div class="c_text">
                                    {{controlItem.typeName}}
                                </div>
                            </div>
                            <!-- 说明文字 -->
                            <div v-if="controlItem.type === 'tips'" class="c_control_explain">
                                <div class="c_title">
                                    说明文字：
                                </div>
                                <div class="c_input_text c_input_color">
                                    <!-- v-model="item.text" -->
                                    <el-input type="textarea" :autosize="{ minRows: 6}" placeholder="请输入" v-model="controlItem.explain">
                                    </el-input>
                                </div>
                            </div>
                            <!-- 明细 -->
                            <div v-else-if="controlItem.type === 'detailed' ">
                                <div class="c_control_name">
                                    <div class="c_title">
                                        控件名称：
                                    </div>
                                    <div class="c_input c_input_color">
                                        <!-- v-model="item.name" -->
                                        <el-input @input="editControlName" :class="{clash:controlItem.isRepeat}" placeholder="请输入名称" v-model="controlItem.name"></el-input>
                                    </div>
                                </div>
                                <div v-if="controlItem.isRepeat" class="repeat">不能设置重复的控件名称</div>
                            </div>

                            <!-- 除说明文字和明细外的控件 -->
                            <div v-else>
                                <div class="c_control_name">
                                    <div class="c_title">
                                        控件名称：
                                    </div>
                                    <div class="c_input c_input_color">
                                        <!-- v-model="item.name" -->
                                        <el-input @input="editControlName" :class="{clash:controlItem.isRepeat}" placeholder="请输入名称" v-model="controlItem.name"></el-input>
                                    </div>
                                </div>
                                <div v-if="controlItem.isRepeat" class="repeat">不能设置重复的控件名称</div>
                                <div class="c_control_explain">
                                    <div class="c_title">
                                        控件说明：
                                    </div>
                                    <div class="c_input_text c_input_color">
                                        <!-- v-model="item.text" -->
                                        <el-input type="textarea" :autosize="{ minRows: 3}" placeholder="请输入" v-model="controlItem.explain">
                                        </el-input>
                                    </div>
                                </div>
                                <div v-if="controlItem.isTemplate === 3" class="c_add_option">
                                    <div class="c_title">
                                        添加选项：
                                    </div>
                                    <div class="c_add_option_right">
                                        <div class="c_add_option_item c_input_color" v-for="(item,index) in controlItem.select" :key="index">
                                            <el-input placeholder="选项名称" v-model="item.val"></el-input>
                                            <div class="c_add_option_reduce" :class="{noReduceSelect: controlItem.select.length === 1}" @click="reduceSelect(index)">
                                                <i class="el-icon-minus"></i>
                                            </div>
                                            <div class="c_add_option_add" @click="addSelect(index)">
                                                <i class="el-icon-plus"></i>
                                            </div>

                                        </div>
                                    </div>
                                </div>

                                <div v-else-if="controlItem.isTemplate === 4" class="c_control_other">
                                    <div class="c_title">
                                        选择方式：
                                    </div>
                                    <div class="c_radio">
                                        <el-radio-group v-model="controlItem.choice">
                                            <el-radio :label="1">单选</el-radio>
                                            <el-radio :label="2">多选</el-radio>
                                        </el-radio-group>
                                    </div>
                                </div>

                                <div class="c_control_other">
                                    <div class="c_title">
                                        其他：
                                    </div>
                                    <div class="c_radio">
                                        <el-checkbox v-model="controlItem.isWrite">必填</el-checkbox>
                                        <el-checkbox v-if="controlItem.type !== 'file'" v-model="controlItem.printing">参与打印</el-checkbox>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>

                </div>
            </div>
        </div>

    </div>
</template>


<script>
    export default {
        data() {
            return {
                templateName: '',
                icon: '',
                activeClass: -1,
                controlList: [{
                        name: '文本',
                        type: 'text'
                    },
                    {
                        name: '多行文字',
                        type: 'textarea'

                    },
                    {
                        name: '数字',
                        type: 'number'
                    },
                    {
                        name: '日期',
                        type: 'date'
                    },
                    {
                        name: '日期+时间',
                        type: 'datehour'
                    },
                    {
                        name: '附件',
                        type: 'file'
                    },
                    {
                        name: '明细',
                        type: 'detailed'
                    },
                    {
                        name: '金额',
                        type: 'price'
                    },
                    {
                        name: '单选框',
                        type: 'select'
                    },
                    {
                        name: '多选框',
                        type: 'checkbox'
                    },
                    {
                        name: '说明文字',
                        type: 'tips'
                    },
                    {
                        name: '成员',
                        type: 'user'
                    },
                    {
                        name: '部门',
                        type: 'department'
                    }

                ],
                list: [],
                company_id: '5d1463a61609d536e0bf2a1e', //企业id
                userid: 'zxp17', //用户id
                isDestruction: true, //在页面关闭前，是否销毁本地缓存
                isAddDetailed: false, //是否在明细里添加子组件
                listIndex: -1, //要添加子控件的明细下标
                controlItem: {},
                isNextStep: true, //能否进入下一步

            }
        },
        methods: {
            getItem(index, obj) {
                if (this.isAddDetailed && obj.type === 'detailed') {
                    this.$zero.ToastWarning('明细控件不能添加明细子控件');
                    return;
                }

                this.activeClass = index
                console.log(obj);
                // function common(obj,list) {
                let id = Math.ceil(Math.random() * 100000)
                this.controlItem = {
                    c_id: id, //控件id
                    name: obj.name, //控件名称
                    type: obj.type, //控件类型
                    typeName: obj.name, // 控件类型名称
                    text: '',
                    explain: '', //控件描述
                    printing: true, //参与打印
                    isWrite: false, //必填
                    choice: null, //选择项 1单选 2多选
                    //下面不是接口参数
                    isShow: true, //是否选中
                    isTemplate: 1, //使用哪个编辑模板， 1默认 2只有控件类型和名称 3多了选择项 4有多选和单选 
                    isRepeat: false, //控件名称是否重复 
                }
                if (obj.type == 'file') {
                    this.controlItem.filePath = [];
                } else if (obj.type == 'detailed') {
                    this.controlItem.detailed = [];
                    this.controlItem.isTemplate = 2;
                } else if (obj.type == 'select' || obj.type == 'checkbox') {
                    this.$set(this.controlItem, 'select', [{
                        val: "选项1"
                    }, {
                        val: "选项2"
                    }]);
                    this.controlItem.isTemplate = 3;
                } else if (obj.type == 'user' || obj.type == 'department') {
                    this.controlItem.choice = 1;
                    this.controlItem.isTemplate = 4;
                } else if (obj.type == 'tips') {
                    this.controlItem.explain = '请输入说明文字'
                }
                for (let i = 0; i < this.list.length; i++) { //遍历原有数组，让其取消选中状态
                    this.list[i].isShow = false;
                }

                if (this.isAddDetailed) {
                    for (let i = 0; i < this.list[this.listIndex].detailed.length; i++) {
                        this.list[this.listIndex].detailed[i].isShow = false;

                        if (this.list[this.listIndex].detailed[i].name === this.controlItem.name) { //检查是否有和新添加的控件名称重复
                            this.list[this.listIndex].detailed[i].isRepeat = true;
                            this.controlItem.isRepeat = true;
                            this.isNextStep = false;
                            break;
                        } 
                    }
                    this.list[this.listIndex].detailed.push(this.controlItem)
                } else {
                    for (let i = 0; i < this.list.length; i++) { //遍历原有数组，检查是否有和新添加的控件名称重复
                        if (this.list[i].name === this.controlItem.name) {
                            this.list[i].isRepeat = true;
                            this.controlItem.isRepeat = true;
                            this.isNextStep = false;
                            console.log('我是谁');
                            console.log(this.isNextStep);
                            
                            break;
                        }
                    }

                    this.list.push(this.controlItem)
                }



            },
            addDetailed(index) { //添加子控件
                console.log('我是子控件');
                console.log(index);
                this.isAddDetailed = true;
                this.listIndex = index;
                for (let i = 0; i < this.list.length; i++) {
                    if (i === index) {
                        this.list[i].isShow = true;
                    } else {
                        this.list[i].isShow = false;
                    }
                }

            },
            delItem(item, index, detailedIndex) { //删除控件
                console.log('我删除了');
                if (item.c_id === this.controlItem.c_id) {
                    this.controlItem.isShow = false;
                }
                console.log(index);

                if (typeof detailedIndex === 'number') {
                    this.list[index].detailed.splice(detailedIndex, 1)
                    // return;
                    if (!item.isRepeat) {
                        return
                    }
                    this.findConflicts(this.list[index].detailed)

                } else {
                    this.list.splice(index, 1)
                    // return;
                    if (!item.isRepeat) {
                        return
                    }
                    this.findConflicts(this.list)
                }

            },
            setModel(item, index, detailedIndex) { //点击模板的控件
                console.log('设置控件');
                this.controlItem = item;
                // this.isEdit = true;
                console.log(item);


                if (typeof detailedIndex === 'number') {

                    this.isAddDetailed = true;
                    this.listIndex = index;

                    for (let i = 0; i < this.list.length; i++) {
                        this.list[i].isShow = false;
                    }
                    for (let i = 0; i < this.list[index].detailed.length; i++) {
                        if (i === detailedIndex) {
                            this.list[index].detailed[i].isShow = true;
                        } else {
                            this.list[index].detailed[i].isShow = false;
                        }
                    }
                } else {
                    this.isAddDetailed = false;
                    for (let i = 0; i < this.list.length; i++) {
                        if (i === index) {
                            this.list[i].isShow = true;
                        } else {
                            this.list[i].isShow = false;
                        }
                        if (this.list[i].detailed) {
                            for (let j = 0; j < this.list[i].detailed.length; j++) {

                                this.list[i].detailed[j].isShow = false;
                            }
                        }
                    }
                }

            },
            editControlName() { //控件名称值改变事件
                console.log(111);
                if (this.isAddDetailed && this.list[this.listIndex].detailed.length > 1) {
                    this.findConflicts(this.list[this.listIndex].detailed)
                } else if (!this.isAddDetailed && this.list.length > 1) {
                    this.findConflicts(this.list)
                }

            },
            findConflicts(list) { //判断设置的控件中有没有控件名称相同的
                for (let i = 0; i < list.length; i++) {
                    let isRepeat = true
                    for (let j = 0; j < list.length; j++) {
                        if (list[i].name === list[j].name && i != j) {
                            isRepeat = false;
                            break;
                        }

                    }
                    if (isRepeat) {
                        list[i].isRepeat = false;
                        this.isNextStep = true;
                    } else {
                        list[i].isRepeat = true;
                        this.isNextStep = false;
                    }
                }
                // return list
            },
            addSelect(index) { //添加选项
                console.log('我来了没？？');
                // let item = JSON.parse(JSON.stringify(this.controlItem.select))

                this.controlItem.select.splice(index + 1, 0, {
                    val: ''
                })
                // console.log(this.controlItem.select);



            },
            reduceSelect(index) { //减少选项
                console.log('我来没？？');
                console.log(this.controlItem.select);


                if (this.controlItem.select.length === 1) {
                    console.log(111);
                    return;
                }
                this.controlItem.select.splice(index, 1)
            },
            async nextStep() {
                // console.log(this.company_id === '5d1463a61609d536e0bf2a1e');
                // return;
                console.log(this.isNextStep);
                
                if (!this.list.length) {
                    this.$zero.ToastWarning('模板内容不能为空');
                    return;
                }
                if (!this.isNextStep) {
                    this.$zero.ToastWarning('控件名称有重复');
                    return;
                }
                let templateName = this.templateName.replace(/^\s*|\s*$/g, "");
                if (templateName === '') {
                    this.$zero.ToastWarning('请输入模板名称');
                    return;
                }

                console.log('进来了');

                this.isDestruction = false;
                // return;
                let ruleTemplate = {
                    company_id: this.company_id,
                    userid: this.userid,
                    name: this.templateName,
                    icon: this.icon,
                    list: this.list
                }
                this.$zero.setSessionStorage({
                    ruleTemplate
                })
                this.$router.push({
                    path: '/procedureSetRuleSettings'
                })
                return;
                let res = await this.$api.process.addTemplate({
                    name: this.templateName,
                    company_id: this.company_id,
                    list: this.list,
                    userid: this.userid
                })

                console.log(res);
                if (res.statusCode !== 75200) {
                    return this.$zero.ToastWarning(res.msg)
                }

                this.$zero.setSessionStorage({
                    addRuleTemplate: 1
                })
                this.$router.push({
                    path: '/procedureSetRuleSettings/' + res._id
                })

            },
            modifyList(list) {
                for (let i = 0; i < list.length; i++) {
                    if (list[i].isShow) {
                        this.controlItem = list[i];
                    } else if (list[i].detailed && list[i].detailed.length > 0) {
                        this.modifyList(list[i].detailed)
                    }
                }
            }
        },

        created() {
            let ruleTemplate = this.$zero.getSessionStorage('ruleTemplate')
            console.log(ruleTemplate);
            if (ruleTemplate) {
                this.list = ruleTemplate.list;
                this.templateName = ruleTemplate.name;
                this.icon = ruleTemplate.icon;
                this.modifyList(this.list)
                this.isNextStep = true;
            }
        },
        beforeDestroy() {
            if (this.isDestruction) {
                this.$zero.removeSessionStorage('ruleTemplate')
            }
        }
    }
</script>


